.data-push {
  margin: 20px;
  padding: 20px;
  background-color: #ffffff;
}
.data-push h2 {
  margin: 5px 0;
  padding: 0;
  font-size: 17px;
  font-weight: 600;
}
.data-push-operaters {
  margin-top: 15px;
}
.data-push-table {
  margin-top: 20px;
}
.data-push-show-detail {
  font-size: 14px;
  color: #40a9ff;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}
.data-push-show-detail:hover {
  color: #1890ff;
}
.data-push-show-detail:active {
  color: #096dd9;
}
.data-push-expan {
  border: 1px solid #888;
  border-radius: 3px;
  padding: 10px;
  max-height: 300px;
  max-width: 1100px;
}
.data-push .ant-table-expand-icon-th,
.data-push .ant-table-row-expand-icon-cell {
  display: none;
}



